import React , {Component} from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {  faArrowDown} from '@fortawesome/free-solid-svg-icons'
class ProductoCadena extends Component{

    render(){
        return(
            <>
                <p class="card-text">{this.props.articulo}</p>
                {this.props.cadena.length-1===this.props.index?
                null
                :
                <p class="card-text"><FontAwesomeIcon className=" ml-1 arrowDownIcon" icon={faArrowDown}  /></p>
                }
            </>
        )
    }
}
export default ProductoCadena