import React, { Component } from "react"
import Pedido from "../components/Pedido"
import Login from "../components/Login"
import CadenaDeProduccion from "../components/CadenaDeProduccion"
import * as firebase from "firebase"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import projectManagement from "../images/projectManagement.jpg"
import { connect } from "react-redux"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowRight} from '@fortawesome/free-solid-svg-icons'

class CadenasDeProduccion extends Component {
    handleChange(e) {
        var forEach = Array.prototype.forEach;
        var arrayProductos = document.getElementsByClassName("card")
        forEach.call(arrayProductos, function (f) {
            console.log()
            if (f.children[0].children[0].innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1) {
                f.className = "card d-none"
            }
            else {
                f.className = "card d-flex"
            }
        });
    }
    alertNuevaCadena=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Nueva Cadena De Produccion</h1>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-left form-group">
                                <p>Ingrese en orden los productos que formen parte de una cadena de produccion</p>
                            </div>
                        </div>
                        <form>
                        <div className="form-row">
                            <div className="col-12 form-group">
                                <input type="text" className="form-control" id='inputNombreCadena' placeholder="Nombre de la Cadena de Produccion"/>
                            </div>
                        </div>
                        <div className="form-row">
                            <div className="col form-group">
                            <select className="form-control form-group" id='inputElementoCadena1' >
                                <option>-</option>
                                    {Object.keys(this.props.productos).map(articulo=>{
                                        return <option >{articulo}</option>
                                    })}
                            </select>
                            <select className="form-control form-group" id='inputElementoCadena2' >
                                <option>-</option>
                                    {Object.keys(this.props.productos).map(articulo=>{
                                        return <option >{articulo}</option>
                                    })}
                            </select>
                            <select className="form-control form-group" id='inputElementoCadena3' >
                                <option>-</option>
                                    {Object.keys(this.props.productos).map(articulo=>{
                                        return <option >{articulo}</option>
                                    })}
                            </select>
                            <select className="form-control form-group" id='inputElementoCadena4' >
                                <option>-</option>
                                    {Object.keys(this.props.productos).map(articulo=>{
                                        return <option >{articulo}</option>
                                    })}
                            </select>
                            <select className="form-control form-group" id='inputElementoCadena5' >
                                <option>-</option>
                                    {Object.keys(this.props.productos).map(articulo=>{
                                        return <option >{articulo}</option>
                                    })}
                            </select>
                            </div>
                        </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.agregarNuevaCadena()
                                            onClose();
                                        }}
                                    >
                                        Agregar Cadena
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    agregarNuevaCadena= async ()=>{
        let arr=[]
        const nombre = document.getElementById('inputNombreCadena').value
        const arrayElementos =[
            document.getElementById('inputElementoCadena1').value,
            document.getElementById('inputElementoCadena2').value,
            document.getElementById('inputElementoCadena3').value,
            document.getElementById('inputElementoCadena4').value,
            document.getElementById('inputElementoCadena5').value
        ]
        arrayElementos.map(async elemento=>{
            if(elemento!=='-'){
                arr.push(elemento)
            }
        })
        await firebase.database().ref().child(this.props.user.uid).child('cadenasDeProduccion').update({
            [nombre]:arr
        })
    }
    render() {
        console.log(this.props.user.uid)
        try {
            const user = this.props.user.email
            if (user) {
                if (this.props.cadenasDeProduccion) {
                    
                    return (
                        <div>
                            <div className="productList">
                                <div className="container-fluid">
                                    <div className="row align-middle">
                                        <div className="col-6 offset-3">
                                            <form>
                                                <div className="formRow">
                                                    <div className="form-group col-12 text-center">
                                                        <input type="text" className="form-control" onChange={
                                                            e => {
                                                                this.handleChange(e)
                                                            }
                                                        } placeholder="Buscar Producto" />
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                        <div className="col">
                                            <button type="button" className="btn btn-success btnProduct" onClick={this.alertNuevaCadena}>
                                                Nueva Cadena De Produccion!
                                            </button>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.keys(this.props.cadenasDeProduccion).map(nombreCadena=>(
                                            <CadenaDeProduccion
                                                nombre={nombreCadena}
                                                cadena={this.props.cadenasDeProduccion[nombreCadena]}
                                            />
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>

                    )
                }
                else {
                    return (
                        <div>
                            <div className="productList">
                                <div className="container">
                                    <div className="row">
                                        <div className="col-12 form-group text-center">
                                            <h3 className="shadowTitle">No Hay Cadenas De Produccion</h3>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                }
            }
        }
        catch{
            return (
                <Login redirect="pedidos" />
            )
        }
    }
}
const mapStateToProps = state => ({
        pedidos: state.pedidos,
        user: state.user,
        productos:state.productos,
        cadenasDeProduccion:state.cadenasDeProduccion
})
export default connect(mapStateToProps, null)(CadenasDeProduccion)
