import React, { Component } from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus } from '@fortawesome/free-solid-svg-icons'
import "./styles/Product.css"
import * as firebase from "firebase"
import {connect} from 'react-redux'

class Product extends Component {
    state={
        imagen:undefined
    }
    async componentDidMount(){
        if(this.props.user.uid==='RSKNrihne8TnWCEvFah4oVx1Bdm1'){
            await firebase.storage().ref().child('imagenes').child(this.props.nombre+'.png').getDownloadURL().then(url=>{
                this.setState({
                    imagen:url
                })
            })
        }
    }
    render() {
        return (
            <div className="product-election bg-dark" onClick={this.props.handleClick}>
                <div className="product-Info">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h6 className="colorWhite textBold mt-2">{this.props.nombre}</h6>
                            </div>
                        </div>
                        {this.state.imagen?
                            <div className="row mt-2 mb-2">
                                <div className="container img">
                                        <img 
                                        src={this.state.imagen}
                                        className="productImg" alt="" />
                                </div>
                            </div>
                        :
                            null
                        }
                        <div className="row">
                            <div className="col-12 form-group text-center">
                                    <span className="badge badge-íll badge-light">$ {this.props.precio}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        user:state.user,
    }
}
export default connect(mapStateToProps, null)(Product)