import React, { Component } from "react"
import PedidoCliente from "./PedidoCliente"
import PedidoProovedor from "./PedidoProovedor"


class Fichapedido extends Component {
    render() {
        if (this.props.data !== undefined) {
            return (
                <div className="container">
                    <div className="row">
                        {
                            Object.keys(this.props.data).reverse().map(key=>(
                                <div className="col-12 col-md-6 col-lg-4 form-group d-flex align-items-center">
                                    {this.props.seccion === "proveedores" ?
                                        <PedidoProovedor data={this.props.data[key]} llave={key}/>
                                        :
                                        <PedidoCliente data={this.props.data[key]} llave={key}  />
                                    }
                                </div>
                            ))
                        }
                    </div>
                </div>
            )
        }
        else {
            return (
                null
            )
        }
    }
}
export default Fichapedido