import React, { Component } from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes,faTrash} from '@fortawesome/free-solid-svg-icons'
import { confirmAlert } from 'react-confirm-alert'; // Import
import "./styles/Product.css"
import * as firebase from "firebase"
import { connect } from "react-redux"

class Product extends Component {
    state={
        imagen:undefined
    }
    async sumar(nombre, cantidad,disminuye) {
        const nuevaCantidad = parseInt(prompt("Ingresar cantidad a sumar"))
        await this.sumarArticulo(nombre, cantidad, nuevaCantidad, this.props.seccion,disminuye)
    }
    async sumarArticulo(nombre, cantidadActual, nuevaCantidad, seccion,disminuye) {
        await firebase.database().ref().child(this.props.user.uid).child(seccion).child(nombre).update({
            cantidad: (cantidadActual + nuevaCantidad)
        })
        if(disminuye){
            disminuye.map(async enlace=>{
                await firebase.database().ref().child(this.props.user.uid).child('productos').child(enlace).update(
                    {
                        cantidad:(this.props.productos[enlace].cantidad-nuevaCantidad)
                    }
                )
            })
        }
    }
    cambiarPrecio = async () =>{
        const nuevoPrecio = prompt("Ingresar el nuevo precio del producto")
        if(nuevoPrecio){
            await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.nombre).update({
                precio: parseFloat(nuevoPrecio)
            })
        }
    }
    eliminarProducto = async () =>{
        confirmAlert({
            title: 'Eliminar Producto?',
            buttons: [
                {
                    label: 'Si, Eliminar',
                    onClick: async () => {
                        await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.nombre).remove()
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    eliminarEnlace = async (nombre,disminuye,enlace) =>{
        confirmAlert({
            title: 'Eliminar Enlace Con '+nombre+'?',
            buttons: [
                {
                    label: 'Si, Eliminar',
                    onClick: async () => {
                        const result = disminuye.filter(valor => valor!==enlace);
                        const datosRef = await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(nombre)
                        await datosRef.update({
                                disminuye:result
                        }
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    async componentDidMount(){
        if(this.props.user.uid==='RSKNrihne8TnWCEvFah4oVx1Bdm1'){
            await firebase.storage().ref().child('imagenes').child(this.props.nombre+'.png').getDownloadURL().then(url=>{
                this.setState({
                    imagen:url
                })
            })
        }
    }
    alertConvertirEnProductoSimple=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            let compuestoPor=[]
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Hacer de {this.props.nombre} un producto simple?</h1>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12">
                                <p>Esta accion es irreversible y si continua se perderan las relaciones entre los productos</p>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-outline-danger'
                                        onClick={() => {
                                            this.convertirEnProductoSimple()
                                            onClose();
                                        }}
                                    >
                                        Convertir en Producto Simple
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    convertirEnProductoSimple= async ()=>{
        await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.nombre).update({
            esElaborado:false,
            compuestoPor:null
        })
    }
    alertConvertirEnProductoCompuesto=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            let compuestoPor=[]
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Hacer De {this.props.nombre} un Producto Compuesto?</h1>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12">
                                <p>El producto compuesto es ideal para combos, promociones o tambien para aquellos conformados por dos o mas productos pero se venden juntos.</p>
                            </div>
                            <div className="col-12">
                                <p>Es decir que al vender uno de estos se modificara la cantidad de existencias de los productos que forman parte.</p>
                            </div>
                            <div className="col-12">
                                <p>Para comenzar seleccione dos articulo que formen parte del producto compuesto</p>
                            </div>
                        </div>
                        <form>
                        <div className="form-row">
                            <div className="col form-group">
                            <select className="form-control" id='inputProductoCompuesto1' >
                                <option>-</option>
                                    {Object.keys(this.props.productos).map(articulo=>{
                                        if(!this.props.productos[this.props.nombre].compuestoPor){
                                            compuestoPor=[]
                                        }
                                        if(!compuestoPor.includes(articulo)){
                                           return <option >{articulo}</option>
                                        }
                                    })}
                            </select>
                            </div>
                        </div>
                        <div className="form-row">
                            <div className="col form-group">
                            <select className="form-control" id='inputProductoCompuesto2' >
                                <option>-</option>
                                    {Object.keys(this.props.productos).map(articulo=>{
                                        if(!this.props.productos[this.props.nombre].compuestoPor){
                                            compuestoPor=[]
                                        }
                                        if(!compuestoPor.includes(articulo)){
                                           return <option >{articulo}</option>
                                        }
                                    })}
                            </select>
                            </div>
                        </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.convertirEnProductoCompuesto()
                                            onClose();
                                        }}
                                    >
                                        Aceptar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    alertAgregarCompuestoPor=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            let compuestoPor=this.props.productos[this.props.nombre].compuestoPor
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Seleccionar un producto para incluirlo en {this.props.nombre}</h1>
                            </div>
                        </div>
                        <form>
                        <div className="form-row">
                            <div className="col form-group">
                            <select className="form-control" id='inputAgregarCompuestoPor' >
                                <option>-</option>
                                    {Object.keys(this.props.productos).map(articulo=>{
                                        if(!this.props.productos[this.props.nombre].compuestoPor){
                                            compuestoPor=[]
                                        }
                                        if(!compuestoPor.includes(articulo)){
                                           return <option >{articulo}</option>
                                        }
                                    })}
                            </select>
                            </div>
                        </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.agregarCompuestoPor()
                                            onClose();
                                        }}
                                    >
                                        Agregar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    agregarCompuestoPor= async ()=>{
        let arr=this.props.productos[this.props.nombre].compuestoPor
        const compuestoPor = document.getElementById('inputAgregarCompuestoPor').value
        if(compuestoPor){
            arr.push(compuestoPor)
            await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.nombre).update({
                compuestoPor:arr
            })
        }
    }
    convertirEnProductoCompuesto= async ()=>{
        const producto1 = document.getElementById('inputProductoCompuesto1').value
        const producto2 = document.getElementById('inputProductoCompuesto2').value
        await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.nombre).update({
            esElaborado:true,
            compuestoPor:{
                0:producto1,
                1:producto2
            }
        })
    }
    alertConfiguracionProducto=(nombre,disminuye)=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    {console.log(this.props.productos[this.props.nombre])}
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Configuracion Del Producto</h1>
                                <h3>{nombre}</h3>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col">
                                <p>En esta seccion podra enlazar ciertos productos para que al aumentar uno disminuya otro por ejemplo</p>
                            </div>
                        </div><div className="row">
                            <div className="col form-group">
                                {this.props.productos[this.props.nombre].esElaborado?
                                (this.props.productos[this.props.nombre].compuestoPor?
                                    <div className="container">
                                        <div className="row">
                                            <div className="col-12 text-center">
                                                <h3>Compuesto Por</h3>
                                            </div>
                                        </div>
                                        {this.props.productos[this.props.nombre].compuestoPor?
                                            <div className="row">
                                                <div className="col">
                                                    {this.props.productos[this.props.nombre].compuestoPor.map(articulo=>(
                                                            <h5>
                                                                <span className='badge badge-pill badge-info'>{articulo} 
                                                                    <FontAwesomeIcon onClick={()=>{this.eliminarCompuestoPor(articulo)}}className=" ml-1 timesIcon" icon={faTimes}  />
                                                                </span> 
                                                            </h5>
                                                    ))}
                                                </div>
                                            </div>
                                        :
                                        null
                                        }
                                        <div className="row">
                                            <div className="col">
                                                <button type='button' className='btn btn-outline-success' onClick={this.alertAgregarCompuestoPor}>Agregar Producto a la Composicion</button>                                
                                            </div>
                                            <div className="col">
                                                <button type='button' className='btn btn-outline-dark' onClick={this.alertConvertirEnProductoSimple}>Convertir En Producto Simple</button>                                
                                            </div>
                                        </div>
                                    </div>
                                    :
                                    null
                                )
                                :
                                <button type='button' className='btn btn-outline-dark' onClick={this.alertConvertirEnProductoCompuesto} >Convertir En Producto Compuesto</button>
                                }
                            </div>
                        </div>
                        <form>
                            <div className="row">
                                <div className="col text-center">
                                    <h6>Al Aumentar {nombre}, disminuir </h6>
                                </div>
                            </div>
                            <div className="row">
                                {
                                    this.props.disminuye?
                                    <div className="col form-group">
                                        {this.props.disminuye.map(enlace=>(
                                            <div className="badge mr-2 badge-pill badge-info">
                                                {enlace}
                                                 <FontAwesomeIcon onClick={()=>{this.eliminarEnlace(nombre,disminuye,enlace)}}className=" ml-1 timesIcon" icon={faTimes}  />
                                            </div>
                                        ))}
                                    </div>
                                    :
                                    null
                                }
                            </div>
                        <div className="form-row">
                            <div className="col form-group">
                            <select className="form-control" id='inputEnlace1' >
                                <option>-</option>
                                    {Object.keys(this.props.productos).map(articulo=>{
                                        if(!disminuye){
                                            disminuye=[]
                                        }
                                        if(!disminuye.includes(articulo)){
                                           return <option >{articulo}</option>
                                        }
                                    })}
                            </select>
                            </div>
                        </div>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-success'
                                        onClick={() => {
                                            this.configuracionProducto(nombre,disminuye)
                                            onClose();
                                        }}
                                    >
                                        Aceptar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-dark'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    configuracionProducto = async (nombre,disminuye) =>{
        let arr = []
        const producto = document.getElementById('inputEnlace1').value
        if(producto!=='-'){
            if(disminuye){
                arr=disminuye
            }
            else{
                arr=[]
            }
            arr.push(producto)
            await firebase.database().ref().child(this.props.user.uid).child('productos').child(nombre).update(
                {
                    disminuye:arr
                }
            )
        }
    }
    eliminarCompuestoPor = async (compuestoPor) =>{
        confirmAlert({
            title: 'Eliminar '+this.props.nombre+' de la composicion del producto?',
            buttons: [
                {
                    label: 'Si, Eliminar',
                    onClick: async () => {
                        const result = this.props.productos[this.props.nombre].compuestoPor.filter(valor => valor!==compuestoPor);
                        console.log(result)
                        if(result.length){
                            const datosRef = await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.nombre)
                            await datosRef.update({
                                    compuestoPor:result
                            }
                            )
                        }
                        else{
                            this.convertirEnProductoSimple()
                        }
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    render() {
        return (
            <div className="container product bg-dark">
                <div className="row">
                    <div className="col bodyProduct">
                        <div className="container">
                            <div className="row">
                                <div className="col-12 mt-2 text-center">
                                    <h5 className="colorWhite nombreProducto " onClick={e=>{
                                        this.alertConfiguracionProducto(this.props.nombre,this.props.productos[this.props.nombre].disminuye)
                                    }}>{this.props.nombre}</h5>
                                    <FontAwesomeIcon className="deleteIcon" icon={faTrash} onClick={this.eliminarProducto} />
                                </div>
                            </div>
                            {this.state.imagen?
                                <div className="row mt-2 mb-2">
                                    <div className="container img">
                                            <img 
                                            src={this.state.imagen} 
                                            className="productImg" alt="" />
                                    </div>
                                </div>
                            :
                                null
                            }
                            <div className="row">
                                <div className="container">
                                    <div className="row">
                                        <div className="col-6 text-left">
                                            <h5 className="colorWhite">Cantidad</h5>
                                        </div>
                                        <div className="col-6 text-right">
                                            <h5><span className="badge badge-light">{this.props.cantidad}</span></h5>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-6 text-left">
                                            <h5 className="colorWhite">Precio</h5>
                                        </div>
                                        <div className="col-6 text-right">
                                            <h5><span className="badge badge-light">$ {this.props.precio}</span></h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row mt-2">
                    <div className="col-6 form-group text-center">
                        <button type="button" className="btn btn-outline-light btnProduct" onClick={async () => {
                            await this.sumar(
                                this.props.nombre,
                                this.props.cantidad,
                                this.props.productos[this.props.nombre].disminuye
                            )
                        }}>
                            Agregar Cantidad!
                        </button>
                    </div>
                    <div className="col-6 form-group text-center">
                        <button type="button" className="btn btn-outline-light btnProduct" onClick={this.cambiarPrecio}>
                            Modificar Precio!
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = state => {
    return {
        user:state.user,
        productos:state.productos,
        cadenasDeProduccion:state.cadenasDeProduccion
    }
}
export default connect(mapStateToProps, null)(Product)