import React, { Component, Fragment } from "react"
import "./styles/PedidoCliente.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTimes, faQuestionCircle } from '@fortawesome/free-solid-svg-icons'
import { confirmAlert } from 'react-confirm-alert'; // Import
import * as firebase from "firebase"
import {connect} from 'react-redux'

class PedidoProovedor extends Component {
    alertEliminarPedido=()=>{
        confirmAlert({
            customUI: ({ onClose }) => {
            return (
                <div className='custom-ui'>
                    <div className="container alert">
                        <div className="row">
                            <div className="col-12 text-center">
                                <FontAwesomeIcon className="alertIcon questionIcon" icon={faQuestionCircle}  />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h1>Eliminar Pedido</h1>
                            </div>
                            <div className="row">
                                <div className="col-12 text-center">
                                    <p>La mercaderia sera descontada</p>
                                </div>
                            </div>
                        </div>
                        <form>
                            <div className="form-row">
                                <div className="col form-group text-center">
                                    <button
                                        type='submit'
                                        className='btn btn-danger'
                                        onClick={() => {
                                            this.eliminarPedido()
                                            onClose();
                                        }}
                                    >
                                        Si, Eliminar
                                    </button>
                                </div>
                                <div className="col text-center">
                                    <button
                                        className='btn btn-outline-danger'
                                        onClick={() => {
                                            onClose();
                                        }}
                                    >
                                        Cancelar
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
              );
            }
        });
    }
    eliminarPedido = async () =>{
        this.props.data.articulos.map(async articulo=>{

            await firebase.database().ref().child(this.props.user.uid).child('productos').child(articulo.nombre).update(
                {
                    cantidad:this.props.productos[articulo.nombre].cantidad-articulo.cantidad
                }
            )
        })
        await firebase.database().ref().child(this.props.user.uid).child('proveedores').child(this.props.proveedor).child('pedidos').child(this.props.llave).remove()
    }
    render() {
        console.log(this.props.data)
        return (
            <div className="container p2 pedido bg-dark">
                <div className="container">
                    <div className="row d-flex align-items-center">
                        <div className="col-10">
                            <h3 className='mt-1 mb-1'>{this.props.data.fecha}</h3>
                        </div>
                        <div className="col-2">
                            <FontAwesomeIcon className='eliminarPedido' onClick={
                                this.alertEliminarPedido
                            } icon={faTimes}  />
                        </div>
                    </div>
                </div>
                <div className="container">
                    {this.props.data.articulos.map(articulo => (
                        <div className="row separator d-flex">
                                <div className=" pt-2 pb-2 ">
                                    <p>{articulo.cantidad} </p>
                                </div>
                                <div className=" pt-2 pb-2 text-right flex-grow-1">
                                    <p>{articulo.nombre} <span className='badge badg  e-pill badge-light'>${articulo.precio}</span></p>
                                </div>
                        </div>
                    ))}
                </div>
            </div>
        )
    }
}
const mapStateToProps=state=>({
    productos:state.productos,
    user:state.user,
    proveedor:state.proveedor
})
export default connect(mapStateToProps,null)(PedidoProovedor)